import React from 'react';
import { BsGithub, BsLinkedin, BsStackOverflow} from 'react-icons/bs';

function Footer() {
    return (
        <div className='footer' bg='primary'>
            <div><BsGithub /> <BsLinkedin /> <BsStackOverflow /></div>
        </div>
    );
};

export default Footer;